// components/AppBar.tsx
'use client'

import { useState } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { FileText, Menu, X } from 'lucide-react'

const AppBar = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false)
    const router = useRouter()

    const navItems = [
        { label: 'Início', href: '/' },
        { label: 'Recursos', href: '#features' },
        { label: 'Como Funciona', href: '#how-it-works' },
        { label: 'Preços', href: '#pricing' },
        { label: 'Contato', href: '#contact' },
    ]

    // Handler para navegação suave
    const handleNavigation = (href: string, e?: React.MouseEvent) => {
        if (e) e.preventDefault()

        if (href.startsWith('#')) {
            // Navegação para seções da mesma página
            const element = document.querySelector(href)
            if (element) {
                element.scrollIntoView({ behavior: 'smooth' })
            }
        } else {
            // Navegação para outras páginas
            router.push(href)
        }

        setIsMenuOpen(false)
    }

    return (
        <nav className="fixed top-0 w-full bg-white/80 backdrop-blur-md border-b border-gray-200 z-50">
            <div className="container-narrow section-padding">
                <div className="flex items-center justify-between h-16">
                    {/* Logo */}
                    <Link href="/" className="flex items-center gap-3">
                        <div className="w-8 h-8 bg-blue-600 rounded-lg flex items-center justify-center">
                            <FileText className="w-5 h-5 text-white" />
                        </div>
                        <span className="text-xl font-bold text-gray-900">
                            Medi<span className="text-blue-600">Translate</span>
                        </span>
                    </Link>

                    {/* Desktop Navigation */}
                    <div className="hidden md:flex items-center gap-8">
                        {navItems.map((item) => (
                            <button
                                key={item.label}
                                onClick={(e) => handleNavigation(item.href, e)}
                                className="text-gray-600 hover:text-blue-600 font-medium transition-colors"
                            >
                                {item.label}
                            </button>
                        ))}
                    </div>

                    {/* CTA Buttons - CORRIGIDO */}
                    <div className="hidden md:flex items-center gap-4">
                        <button
                            onClick={() => router.push('/Login')}
                            className="text-gray-600 hover:text-blue-600 font-medium transition-colors"
                        >
                            Entrar
                        </button>
                        <button
                            onClick={() => router.push('/Signup')}
                            className="btn-primary"
                        >
                            Cadastrar
                        </button>
                    </div>

                    {/* Mobile Menu Button */}
                    <button
                        className="md:hidden"
                        onClick={() => setIsMenuOpen(!isMenuOpen)}
                        type="button" // IMPORTANTE: Define como button
                    >
                        {isMenuOpen ? (
                            <X className="w-6 h-6 text-gray-700" />
                        ) : (
                            <Menu className="w-6 h-6 text-gray-700" />
                        )}
                    </button>
                </div>

                {/* Mobile Menu - CORRIGIDO */}
                {isMenuOpen && (
                    <div className="md:hidden py-4 border-t border-gray-200">
                        <div className="flex flex-col gap-4">
                            {navItems.map((item) => (
                                <button
                                    key={item.label}
                                    onClick={(e) => handleNavigation(item.href, e)}
                                    className="text-gray-600 hover:text-blue-600 font-medium py-2 text-left"
                                >
                                    {item.label}
                                </button>
                            ))}
                            <div className="flex flex-col gap-3 pt-4">
                                <button
                                    onClick={() => {
                                        router.push('/Login')
                                        setIsMenuOpen(false)
                                    }}
                                    className="text-center text-gray-600 hover:text-blue-600 font-medium py-2"
                                >
                                    Entrar
                                </button>
                                <button
                                    onClick={() => {
                                        router.push('/Signup')
                                        setIsMenuOpen(false)
                                    }}
                                    className="btn-primary w-full"
                                >
                                    Cadastrar
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </nav>
    )
}

export default AppBar